.container {
  background-color: aliceblue;
  padding: 1rem 0rem;
  width: 100%;
  max-width: unset;
}
.main {
  text-align: center;
  padding: 1rem 0rem;
  font-size: 20px;
}
.main .title1 {
  font-weight: bold;
}
.main-title2 {
  font-weight: 600;
}
table {
  border-collapse: collapse;
  width: 100%;
}
td,
th {
  border: 1px solid #dddddd;
  border-left: unset;
  border-right: unset;
  text-align: left;
  padding: 8px;
  width: 30%;
}
.table-head td {
  font-weight: bold;
}
tr:nth-child(even) {
  background-color: #dddddd;
}
section {
  padding: 0rem 1rem;
  margin-bottom: 1rem;
}
.section-title {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 10px;
}
.btn-add {
  width: 100%;
}
.btns-user {
  display: flex;
}
.btns-user .btn {
  margin: 0px 5px;
  padding: 2px 8px;
  font-size: 12px;
}
#edit-popup {
  display: none;
  width: 90%;
  position: fixed;
  z-index: 1;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  max-width: 1100px;
}
#edit-popup .card {
  box-shadow: 0px 0px 14px 2px #dedede;
  position: relative;
}
#edit-popup .card-body {
  padding: 2rem 3rem;
}
.close-edit {
  position: absolute;
  right: 16px;
  top: 0px;
  font-weight: bold;
  font-size: xx-large;
  cursor: pointer;
}

@media only screen and (max-width: 767px) {
  .container {
    max-width: unset;
    width: 100%;
  }
}
